.contacts {
  &__info {
    margin: 0 0 24px;
  }

  &__info-title {
    margin: 0 0 8px;

    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: #9fadc2;
  }

  &__info-link {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: #3e3e3e;
  }

  &__field {
    margin: 0 0 16px;

    &--last {
      margin: 0 0 46px;
    }
  }
}
